import { difference } from "lodash";
import * as THREE from "three";
import {
  getBeadIdFromThreeName,
  getThreeNameFromBeadId,
} from "../../../editorObjects/Bead";
import {
  EditBBoxChildSuffix,
  getEditBBoxIdFromThreeName,
  getThreeNameFromEditBBoxId,
} from "../../../editorObjects/EditBBox";
import { EditBBox } from "../../../editorObjects/Editor";
import { buildPoint } from "../../../editorObjects/Point";
import {
  getRegionIdFromThreeName,
  getThreeNameFromRegionId,
  RegionChildSuffix,
} from "../../../editorObjects/Region";
import { appStore } from "../../../immerReducers/appStore";
import { Actions } from "../../../immerReducers/store";
import { appThree } from "../../renderThreejs";
import { MouseDragHelper } from "../MouseDragHelper";
import { MouseSelectionHelper } from "../MouseSelectionHelper";
import { screenToWorld } from "../screenToWorld";
import { SelectionFilter } from "../SelectionFilter";
import { MouseEventsBase } from "./MouseEventsBase";

export class MouseEventsEditorModeSelectMultiple extends MouseEventsBase {
  isCameraGrabed: boolean;
  startDragMousePosition: THREE.Vector2;
  startDragCamera: THREE.Vector2;
  boundingClientRect: DOMRect | undefined;
  mouseDragHelper: MouseDragHelper;
  mouse: THREE.Vector2;
  raycaster: THREE.Raycaster;
  selectionBox: SelectionFilter;
  mouseSelectionHelper: MouseSelectionHelper;
  prevEditBBox: any | undefined;

  constructor() {
    super();
    this.boundingClientRect = undefined;
    this.isCameraGrabed = false;
    this.startDragMousePosition = new THREE.Vector2();
    this.startDragCamera = new THREE.Vector2();
    this.mouseDragHelper = new MouseDragHelper();
    this.mouse = new THREE.Vector2();
    this.raycaster = new THREE.Raycaster();

    this.mouseSelectionHelper = new MouseSelectionHelper();
    this.mouseSelectionHelper.setRenderer(appThree.renderer);

    this.selectionBox = new SelectionFilter();
    this.selectionBox.setCamera(appThree.camera);
    this.selectionBox.setScene(appThree.scene);
  }

  onWheel(event: PointerEvent) {}

  onResize(event: PointerEvent) {}

  onDown(event: PointerEvent) {
    if (appThree.el !== null) {
      this.boundingClientRect = appThree.el.getBoundingClientRect();
    }

    if (this.boundingClientRect === undefined) {
      return;
    }
    const { getState } = appStore;
    const state = getState();

    const { x, y } = screenToWorld(event.clientX, event.clientY);
    this.mouseDragHelper.onDown(x, y);

    if (
      state.ui.selectedBeadsIdsCount === 0 &&
      state.ui.selectedRegionsIdsCount === 0
    ) {
      const { width, height, top, left } = this.boundingClientRect;
      const screenX = event.clientX - left;
      const screenY = event.clientY - top;
      this.mouseSelectionHelper.onDown(screenX, screenY);
      this.selectionBox.startPoint.set(
        ((event.clientX - left) / width) * 2 - 1,
        -((event.clientY - top) / height) * 2 + 1,
        0
      );
    } else {
      const { width, height, top, left } = this.boundingClientRect;
      this.mouse.x = ((event.clientX - left) / width) * 2 - 1;
      this.mouse.y = -((event.clientY - top) / height) * 2 + 1;
      this.raycaster.setFromCamera(this.mouse, appThree.camera);

      const intersects = this.raycaster.intersectObjects(
        appThree.scene.children,
        true
      );

      if (intersects.length > 0) {
      }
      const beadsIds: string[] = [];
      const regionIds: string[] = [];
      const editBBoxIds: string[] = [];
      intersects.forEach((e) => {
        const name = e.object.name;
        const beadId = getBeadIdFromThreeName(name);
        if (beadId !== undefined) {
          if (state.appData.beads[beadId]) {
            if (beadsIds.includes(beadId) === false) {
              beadsIds.push(beadId);
              return;
            }
          } else {
            console.warn("bead id missing in store", beadId);
          }
        }

        const regionId = getRegionIdFromThreeName(name);
        if (regionId !== undefined) {
          if (state.appData.regions[regionId]) {
            if (regionIds.includes(regionId) === false) {
              regionIds.push(regionId);
              return;
            }
          } else {
            console.warn("region id missing in store", regionId);
          }
        }

        const editBBoxId = getEditBBoxIdFromThreeName(name);
        if (editBBoxId !== undefined) {
          if (state.ui.editBBox && state.ui.editBBox.id === editBBoxId) {
            if (editBBoxIds.includes(editBBoxId) === false) {
              editBBoxIds.push(editBBoxId);
              return;
            }
          } else {
            console.warn("editbbox id missing in store", editBBoxId);
          }
        }
      });

      if (editBBoxIds.length > 0) {
        this.mouseDragHelper.shouldBehaveLikeMove = true;

        if (state.ui.editBBox) {
          const name = getThreeNameFromEditBBoxId(state.ui.editBBox.id);
          const object = appThree.scene.getObjectByName(name);
          if (object !== undefined) {
            this.prevEditBBox = {
              id: state.ui.editBBox.id,
              x: object.position.x,
              y: object.position.y,
            };

            const nameTL = getThreeNameFromEditBBoxId(
              state.ui.editBBox.id,
              EditBBoxChildSuffix.DOT_RESIZE_TOP_LEFT
            );
            const objectTL = appThree.scene.getObjectByName(nameTL);
            if (objectTL !== undefined) {
              this.prevEditBBox.resizeXTL = objectTL.position.x;
              this.prevEditBBox.resizeYTL = objectTL.position.y;
            }

            const nameTR = getThreeNameFromEditBBoxId(
              state.ui.editBBox.id,
              EditBBoxChildSuffix.DOT_RESIZE_TOP_RIGHT
            );
            const objectTR = appThree.scene.getObjectByName(nameTR);
            if (objectTR !== undefined) {
              this.prevEditBBox.resizeXTR = objectTR.position.x;
              this.prevEditBBox.resizeYTR = objectTR.position.y;
            }

            const nameBL = getThreeNameFromEditBBoxId(
              state.ui.editBBox.id,
              EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_LEFT
            );
            const objectBL = appThree.scene.getObjectByName(nameBL);
            if (objectBL !== undefined) {
              this.prevEditBBox.resizeXBL = objectBL.position.x;
              this.prevEditBBox.resizeYBL = objectBL.position.y;
            }
            const nameBR = getThreeNameFromEditBBoxId(
              state.ui.editBBox.id,
              EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_RIGHT
            );
            const objectBR = appThree.scene.getObjectByName(nameBR);
            if (objectBR !== undefined) {
              this.prevEditBBox.resizeXBR = objectBR.position.x;
              this.prevEditBBox.resizeYBR = objectBR.position.y;
            }
          }
        } else {
          this.prevEditBBox = undefined;
        }
      } else {
        const diffBeads = difference(
          beadsIds,
          Object.keys(state.ui.selectedBeadsIds)
        );

        const diffRegions = difference(
          regionIds,
          Object.keys(state.ui.selectedRegionsIds)
        );

        if (
          (beadsIds.length > 0 && diffBeads.length === 0) ||
          (regionIds.length > 0 && diffRegions.length === 0)
        ) {
          this.mouseDragHelper.shouldBehaveLikeMove = true;

          if (state.ui.editBBox) {
            const name = getThreeNameFromEditBBoxId(state.ui.editBBox.id);
            const object = appThree.scene.getObjectByName(name);
            if (object !== undefined) {
              this.prevEditBBox = {
                id: state.ui.editBBox.id,
                x: object.position.x,
                y: object.position.y,
              };

              const nameTL = getThreeNameFromEditBBoxId(
                state.ui.editBBox.id,
                EditBBoxChildSuffix.DOT_RESIZE_TOP_LEFT
              );
              const objectTL = appThree.scene.getObjectByName(nameTL);
              if (objectTL !== undefined) {
                this.prevEditBBox.resizeXTL = objectTL.position.x;
                this.prevEditBBox.resizeYTL = objectTL.position.y;
              }

              const nameTR = getThreeNameFromEditBBoxId(
                state.ui.editBBox.id,
                EditBBoxChildSuffix.DOT_RESIZE_TOP_RIGHT
              );
              const objectTR = appThree.scene.getObjectByName(nameTR);
              if (objectTR !== undefined) {
                this.prevEditBBox.resizeXTR = objectTR.position.x;
                this.prevEditBBox.resizeYTR = objectTR.position.y;
              }
              const nameBL = getThreeNameFromEditBBoxId(
                state.ui.editBBox.id,
                EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_LEFT
              );
              const objectBL = appThree.scene.getObjectByName(nameBL);
              if (objectBL !== undefined) {
                this.prevEditBBox.resizeXBL = objectBL.position.x;
                this.prevEditBBox.resizeYBL = objectBL.position.y;
              }
              const nameBR = getThreeNameFromEditBBoxId(
                state.ui.editBBox.id,
                EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_RIGHT
              );
              const objectBR = appThree.scene.getObjectByName(nameBR);
              if (objectBR !== undefined) {
                this.prevEditBBox.resizeXBR = objectBR.position.x;
                this.prevEditBBox.resizeYBR = objectBR.position.y;
              }
            }
          } else {
            this.prevEditBBox = undefined;
          }
        } else {
          const { width, height, top, left } = this.boundingClientRect;
          const screenX = event.clientX - left;
          const screenY = event.clientY - top;
          this.mouseSelectionHelper.onDown(screenX, screenY);
          this.selectionBox.startPoint.set(
            ((event.clientX - left) / width) * 2 - 1,
            -((event.clientY - top) / height) * 2 + 1,
            0
          );
        }
      }
    }
  }

  onMove(event: PointerEvent) {
    if (this.boundingClientRect === undefined) {
      return;
    }
    const { getState, dispatch } = appStore;
    const state = getState();

    const { width, height, top, left } = this.boundingClientRect;

    if (this.mouseDragHelper.shouldBehaveLikeMove === true) {
      const { x, y } = screenToWorld(event.clientX, event.clientY);
      this.mouseDragHelper.onMove(x, y);
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;
      Object.keys(state.ui.selectedBeadsIds).forEach((beadId) => {
        const name = getThreeNameFromBeadId(beadId);
        const object = appThree.scene.getObjectByName(name);
        if (object !== undefined) {
          object.position.x = state.appData.beads[beadId].position.x + diffX;
          object.position.y = state.appData.beads[beadId].position.y + diffY;
        }
      });
      Object.keys(state.ui.selectedRegionsIds).forEach((regionId) => {
        const name = getThreeNameFromRegionId(
          regionId,
          RegionChildSuffix.CONTENT
        );
        const object = appThree.scene.getObjectByName(name);
        if (object !== undefined) {
          object.position.x =
            state.appData.regions[regionId].position.x + diffX;
          object.position.y =
            state.appData.regions[regionId].position.y + diffY;
        }
      });

      if (this.prevEditBBox) {
        const nameA = getThreeNameFromEditBBoxId(this.prevEditBBox.id);
        const editBBox = appThree.scene.getObjectByName(nameA);
        if (editBBox !== undefined) {
          editBBox.position.x = this.prevEditBBox.x + diffX;
          editBBox.position.y = this.prevEditBBox.y + diffY;
        }

        const nameTL = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_TOP_LEFT
        );
        const objectTL = appThree.scene.getObjectByName(nameTL);
        if (objectTL !== undefined) {
          objectTL.position.x = this.prevEditBBox.resizeXTL + diffX;
          objectTL.position.y = this.prevEditBBox.resizeYTL + diffY;
        }

        const nameTR = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_TOP_RIGHT
        );
        const objectTR = appThree.scene.getObjectByName(nameTR);
        if (objectTR !== undefined) {
          objectTR.position.x = this.prevEditBBox.resizeXTR + diffX;
          objectTR.position.y = this.prevEditBBox.resizeYTR + diffY;
        }
        const nameBL = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_LEFT
        );
        const objectBL = appThree.scene.getObjectByName(nameBL);
        if (objectBL !== undefined) {
          objectBL.position.x = this.prevEditBBox.resizeXBL + diffX;
          objectBL.position.y = this.prevEditBBox.resizeYBL + diffY;
        }
        const nameBR = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_RIGHT
        );
        const objectBR = appThree.scene.getObjectByName(nameBR);
        if (objectBR !== undefined) {
          objectBR.position.x = this.prevEditBBox.resizeXBR + diffX;
          objectBR.position.y = this.prevEditBBox.resizeYBR + diffY;
        }
      }
    } else {
      if (this.mouseDragHelper.isDragStarted === true) {
        const { x, y } = screenToWorld(event.clientX, event.clientY);

        this.mouseDragHelper.onMove(x, y);

        if (this.mouseDragHelper.isDragStarted === true) {
          this.selectionBox.endPoint.set(
            ((event.clientX - left) / width) * 2 - 1,
            -((event.clientY - top) / height) * 2 + 1,
            1
          );

          const allSelected = this.selectionBox.select();
          const beadsIds: string[] = [];
          const regionIds: string[] = [];
          allSelected.forEach((object) => {
            const beadId = getBeadIdFromThreeName(object.name);
            if (beadId !== undefined) {
              if (state.appData.beads[beadId]) {
                if (beadsIds.includes(beadId) === false) {
                  beadsIds.push(beadId);
                }
              } else {
                console.warn("bead id missing in store", beadId);
              }
            }

            const regionId = getRegionIdFromThreeName(object.name);
            if (regionId !== undefined) {
              if (state.appData.regions[regionId]) {
                if (regionIds.includes(regionId) === false) {
                  regionIds.push(regionId);
                }
              } else {
                console.warn("region id missing in store", regionId);
              }
            }
          });

          dispatch(
            Actions.MouseSelection.action_selectIntersectObjects(
              beadsIds,
              regionIds
            )
          );
        }
      }

      const screenX = event.clientX - left;
      const screenY = event.clientY - top;

      this.mouseSelectionHelper.onMove(screenX, screenY);
    }
  }

  onUp(event: PointerEvent) {
    const { getState, dispatch } = appStore;
    const state = getState();
    if (this.mouseDragHelper.shouldBehaveLikeMove === true) {
      const diffX = this.mouseDragHelper.end.x - this.mouseDragHelper.start.x;
      const diffY = this.mouseDragHelper.end.y - this.mouseDragHelper.start.y;
      if (this.prevEditBBox) {
        const name = getThreeNameFromEditBBoxId(this.prevEditBBox.id);
        const object = appThree.scene.getObjectByName(name);
        if (object !== undefined) {
          object.position.x = this.prevEditBBox.x + diffX;
          object.position.y = this.prevEditBBox.y + diffY;
        }

        const nameTL = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_TOP_LEFT
        );
        const objectTL = appThree.scene.getObjectByName(nameTL);
        if (objectTL !== undefined) {
          objectTL.position.x = this.prevEditBBox.resizeXTL + diffX;
          objectTL.position.y = this.prevEditBBox.resizeYTL + diffY;
        }

        const nameTR = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_TOP_RIGHT
        );
        const objectTR = appThree.scene.getObjectByName(nameTR);
        if (objectTR !== undefined) {
          objectTR.position.x = this.prevEditBBox.resizeXTR + diffX;
          objectTR.position.y = this.prevEditBBox.resizeYTR + diffY;
        }
        const nameBL = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_LEFT
        );
        const objectBL = appThree.scene.getObjectByName(nameBL);
        if (objectBL !== undefined) {
          objectBL.position.x = this.prevEditBBox.resizeXBL + diffX;
          objectBL.position.y = this.prevEditBBox.resizeYBL + diffY;
        }
        const nameBR = getThreeNameFromEditBBoxId(
          this.prevEditBBox.id,
          EditBBoxChildSuffix.DOT_RESIZE_BOTTOM_RIGHT
        );
        const objectBR = appThree.scene.getObjectByName(nameBR);
        if (objectBR !== undefined) {
          objectBR.position.x = this.prevEditBBox.resizeXBR + diffX;
          objectBR.position.y = this.prevEditBBox.resizeYBR + diffY;
        }
      }
      this.mouseDragHelper.shouldBehaveLikeMove = false;
      this.prevEditBBox = undefined;
      dispatch(Actions.MouseSelection.action_movedSelected(diffX, diffY));
    } else {
      if (this.mouseDragHelper.isDragged === true) {
        // do nothing
      } else if (
        this.mouseDragHelper.isDragged === false &&
        this.mouseDragHelper.isDragStarted === true
      ) {
        if (this.boundingClientRect === undefined) {
          return;
        }

        const { width, height, top, left } = this.boundingClientRect;
        this.mouse.x = ((event.clientX - left) / width) * 2 - 1;
        this.mouse.y = -((event.clientY - top) / height) * 2 + 1;
        this.raycaster.setFromCamera(this.mouse, appThree.camera);
        const intersects = this.raycaster.intersectObjects(
          appThree.scene.children,
          true
        );
        const beadsIds: string[] = [];
        const regionIds: string[] = [];
        intersects.forEach((e) => {
          const name = e.object.name;
          const beadId = getBeadIdFromThreeName(name);
          if (beadId !== undefined) {
            if (state.appData.beads[beadId]) {
              if (beadsIds.includes(beadId) === false) {
                beadsIds.push(beadId);
              }
            } else {
              console.warn("bead id missing in store", beadId);
            }
          }

          const regionId = getRegionIdFromThreeName(name);
          if (regionId !== undefined) {
            if (state.appData.regions[regionId]) {
              if (regionIds.includes(regionId) === false) {
                regionIds.push(regionId);
              }
            } else {
              console.warn("region id missing in store", regionId);
            }
          }
        });

        dispatch(
          Actions.MouseSelection.action_selectIntersectObjects(
            beadsIds,
            regionIds
          )
        );
      } else {
        dispatch(Actions.MouseSelection.action_selectIntersectObjects([], []));
      }
    }
    this.mouseDragHelper.onUp();
    this.mouseSelectionHelper.onUp();
  }
}
