import { immerable } from "immer";
import { DeepPartial } from "ts-essentials";

export interface Point {
  x: number;
  y: number;
  // [immerable]: boolean;
}

export enum BeadTypes {
  TOHO_MIYUKI = "TOHO_MIYUKI",
  TOHO_15 = "TOHO_15",
  TOHO_SPHERE = "TOHO_SPHERE",
}

export interface AppObjectProps {
  id: string;
  position: Point;
  // [immerable]: boolean;
  width: number;
  height: number;
  depth: number;
  rotateX: number;
  rotateY: number;
  rotateZ: number;
  objectType: ObjectTypes;
  isLocked: boolean;
  groupId: string | undefined;
  canLockOrUnlock: boolean;
}

export interface PreviewProject3D extends AppObjectProps {
  color: string;
}

export interface Bead extends AppObjectProps {
  type: BeadTypes;
  color: string;
  lineRotate: number;
  keepAspectRatio: boolean;
}

export interface EditBBox {
  id: string;
  position: Point;
  // [immerable]: boolean;
  width: number;
  height: number;
  depth: number;
  rotateX: number;
  rotateY: number;
  rotateZ: number;
  editable: boolean;
  selectedIds: {
    id: string;
    objectType: ObjectTypes;
    groupId: string | undefined;
  }[];
  canLockOrUnlock: true;
  isLocked: false;
  objectType: ObjectTypes;
  bbox?: THREE.Box3;
}

export interface Line {
  id: string;
  start: Point;
  end: Point;
  beadIds: string[];
  // [immerable]: boolean;
  settings: {
    beadsCount: number;
    beadsAutoCount: boolean;
    beadsSpacing: number;
    beadsAutoSpacing: boolean;
    defaultBead: Partial<Bead>;
  };
}

export enum RegionGridType {
  Rectangles = "Rectangles",
}

export interface Region extends AppObjectProps {
  beadIds: string[];
  gridType: RegionGridType;
  opacity: number;
  settings: {
    columns: number;
    rows: number;
    // beadsCount: number;
    // beadsAutoCount: boolean;
    // beadsSpacing: number;
    // beadsAutoSpacing: boolean;
    // defaultBead: Partial<Bead>;
  };
}

export interface Group extends AppObjectProps {
  objectType: ObjectTypes;
  childrenIds: string[];
}

export interface AppData {
  objects: { [s: string]: AppObject };
  /**@deprecated */
  beads: { [s: string]: Bead };
  /**@deprecated */
  lines: { [s: string]: Line };
  /**@deprecated */
  regions: { [s: string]: Region };
}

export type AppObject = Bead | Region | Group | PreviewProject3D;
export type AppObjectKeys = keyof Bead | keyof Region | keyof Group | keyof PreviewProject3D;


// const dupa: MappedTypeWithNewProperties<AppObject> = {
//   beadIds: 
// }

export enum ObjectTypes {
  Bead = "Bead",
  Region = "Region",
  Group = "Group",
  PreviewProject3D = "PreviewProject3D",
  SceneEditBBox = "SceneEditBBox",
  SceneBackgroundGrid = "SceneBackgroundGrid",
  SceneLight = "SceneLight",
}
export const NAME_SEPARATOR = "__";

export enum EditorMode {
  Idle = "Idle",
  EditLines = "EditLines",
  AddLine = "AddLine",
  SelectOrMove = "SelectOrMove",
  SelectMultiple = "SelectMultiple",
  AddBeads = "AddBeads",
  DeleteBeads = "DeleteBeads",
  PaintBeads = "ColorPaint",
  AddRegion = "AddRegion",
}
export enum EditorMenuMain {
  None = "None",
  File = "File",
  Grid = "Grid",
  Background = "Background",
}
export enum EditorGridType {
  Hex = "Hex",
  Box = "Box",
}
export enum BrushType {
  Color = "Color",
}
export interface ThreeLightConfig {
  id: string;
  type: "THREE.DirectionalLight" | "THREE.AmbientLight";
  name: string;
  color?: string | number | THREE.Color | undefined;
  intensity?: number | undefined;
  position?: {
    x: number;
    y: number;
    z: number;
  };
}

export interface ThreeGridHelperConfig{
  id: string;
  type: EditorGridType;
  hexSize: number;
  snapEnabled: boolean;
  isVisible: boolean;
  width: number;
  height: number;
}


export interface EditorUi {
  lights: ThreeLightConfig[];
  zoomScale: number;
  lineSettings: DeepPartial<Line>;
  saveInfo: {
    saved: boolean;
    haveUnsavedChanges: boolean;
  };
  /**@deprecated */
  selectedLinesIds: { [s: string]: boolean };
  /**@deprecated */
  selectedLinesIdsCount: number;
  /**@deprecated */
  selectedBeadsIds: { [s: string]: boolean };
  /**@deprecated */
  selectedBeadsIdsCount: number;
  /**@deprecated */
  selectedRegionsIds: { [s: string]: boolean };
  /**@deprecated */
  selectedRegionsIdsCount: number;
  selectedIds: string[];
  selectedIdsAsKeys: { [s: string]: boolean };
  selectedIdsCount: number;
  selectedTopBarPosition: THREE.Vector3 | undefined;
  editBBox: EditBBox | undefined;
  mouseSelection: {
    start: Point;
    end: Point;
    isDragStarted: boolean;
    isDragged: boolean;
  };
  currentMenuMainOpen: EditorMenuMain;
  grid: ThreeGridHelperConfig;
  currentBeadChain: {
    isDragEnabled: boolean;
    beadIds: string[];
  };
  beadsTemplate: {
    width: number;
    height: number;
    depth: number;
    type: BeadTypes;
    color: string;
    rotateX: number;
    rotateY: number;
    rotateZ: number;
    keepAspectRatio: boolean;
  };
  regionTemplate: {
    gridType: RegionGridType;
  };
  brush: {
    type: BrushType;
    size: number;
    color: string;
  };
  background: {
    image: {
      url: string;
      scale: number;
      position: Point;
      opacity: number;
    };
  };
  sceneWrapperWidth: number;
  sceneWrapperHeight: number;
  sceneWrapperPosition: {
    x: number;
    y: number;
    angle: number;
    scale: number;
    top: number;
    left: number;
  };
}

export interface Editor {
  appData: AppData;
  ui: EditorUi;
  mode: EditorMode;
}
