import { Editor } from "../editorObjects/Editor";
import { ImmerReducer } from "immer-reducer";
import { Store } from "../controllers/store";

export class MainImmerReducer extends ImmerReducer<Editor> {
  /** @deprecated */
  action_test() {}
  /** @deprecated */
  action_restoreState() {}
  updateEditorState(newState: Store) {
    this.draftState.appData.objects = newState.objects;
    this.draftState.ui.selectedIds = newState.selectedIds;
    this.draftState.ui.selectedTopBarPosition =
      newState.ui.selectedTopBarPosition;
    this.draftState.ui.lights = newState.ui.lights;
    this.draftState.ui.grid = newState.ui.grid;
    this.draftState.ui.zoomScale = newState.ui.zoomScale;
    this.draftState.mode = newState.ui.mode;
  }
}
