import * as THREE from "three";
import {
  AppObject,
  EditorGridType,
  EditorMode,
  ThreeGridHelperConfig,
  ThreeLightConfig,
} from "../editorObjects/Editor";

export interface StoreUiSettings {
  zoomScale: number;
  mode: EditorMode;
  lights: ThreeLightConfig[];
  grid: ThreeGridHelperConfig;
  selectedTopBarPosition: THREE.Vector3 | undefined;
}
export interface Store {
  objects: { [s: string]: AppObject };
  selectedIds: string[];
  ui: StoreUiSettings;
}

let store: Store;

export const initStore = () => {
  store = {
    objects: {},
    selectedIds: [],
    ui: {
      zoomScale: 1,
      mode: EditorMode.SelectOrMove,
      lights: [
        {
          id: THREE.MathUtils.generateUUID(),
          type: "THREE.DirectionalLight",
          name: "main_light_1",
          color: "#FFF",
          intensity: 0.8,
          position: {
            x: 0,
            y: 0,
            z: 10,
          },
        },
        {
          id: THREE.MathUtils.generateUUID(),
          type: "THREE.DirectionalLight",
          name: "main_light_2",
          color: "#FFF",
          intensity: 0.4,
          position: {
            x: 0,
            y: 0,
            z: 10,
          },
        },
      ],
      grid: {
        id: THREE.MathUtils.generateUUID(),
        type: EditorGridType.Hex,
        hexSize: 30,
        snapEnabled: true,
        isVisible: true,
        width: 30,
        height: 30,
      },
      selectedTopBarPosition: undefined
    },
  };
};

export const getStore = () => {
  return store;
};

export const setStore = (newStore: Store) => {
  console.log("nowyj store", newStore.objects, newStore.selectedIds);
  store = newStore;
};
