import produce from "immer";
import { AppEventType, ClipboardPaste } from "../AppEventType";
import { setStore } from "../store";
import { ClipboardPasteHistory } from "../AppHistory";
import { addObjectByObjectType } from "../../threejs/eventEmitterHandlers";
import { AppObject, Group, ObjectTypes } from "../../editorObjects/Editor";
import { refreshReactState } from "../refreshReactState";
import { StoreObjectHandler } from "../BaseHandlers/StoreObjectHandler";
import { getMousePositionAtWindow } from "../../threejs/mouseListeners/mouseScreenPosition";
import { appThree } from "../../threejs/renderThreejs";
import { screenToWorld } from "../../threejs/mouseListeners/screenToWorld";
import * as THREE from "three";
import { cloneDeep } from "lodash";

/**
 * SUPER POWER!!!
 */
export class ClipboardPasteHandler extends StoreObjectHandler<ClipboardPaste> {
  run() {
    const mapIds = {};
    const objects: AppObject[] = [];
    this.event.payload.objects.forEach((obj) => {
      if (!obj.id) {
        return;
      }
      const oldId = obj.id;
      const clone = cloneDeep(obj)
      delete (clone as any).id;
      let newObject = this.getAppObjectFromCloneAppObject(clone);
      if (newObject == undefined) {
        return;
      }
      mapIds[oldId] = newObject.id;
      objects.push(newObject);
    });
    let posX = 0;
    let posY = 0;
    const pos = getMousePositionAtWindow();
    const { width, height, top, left } = appThree.el.getBoundingClientRect();
    if (pos === undefined) {
      posX = left + width / 2;
      posY = top + height / 2;
    } else {
      posX = pos.x;
      posY = pos.y;
    }
    const worldPos = screenToWorld(posX, posY);
    const bbox = new THREE.Box3();
    objects.forEach((object) => {
      const w = object.width / 2;
      const h = object.height / 2;
      const x = object.position.x;
      const y = object.position.y;
      bbox.expandByPoint(new THREE.Vector3().set(x - w, y - h, 0.1));
      bbox.expandByPoint(new THREE.Vector3().set(x + w, y - h, 0.2));
      bbox.expandByPoint(new THREE.Vector3().set(x - w, y + h, 0.1));
      bbox.expandByPoint(new THREE.Vector3().set(x + w, y + h, 0.1));
    });
    const bboxCenter = new THREE.Vector3();
    bbox.getCenter(bboxCenter);
    const diffX = worldPos.x - bboxCenter.x;
    const diffY = worldPos.y - bboxCenter.y;
    objects.forEach((object) => {
      if (object.objectType === ObjectTypes.Group) {
        const newChildrenIds: string[] = [];
        (object as Group).childrenIds.forEach((id) => {
          newChildrenIds.push(mapIds[id]);
        });
        (object as Group).childrenIds = newChildrenIds;
        object.position.x = object.position.x + diffX;
        object.position.y = object.position.y + diffY;
        objects.forEach((obj) => {
          if (newChildrenIds.includes(obj.id)) {
            obj.position.x = obj.position.x + diffX;
            obj.position.y = obj.position.y + diffY;
          }
        });
      } else {
        if (object.groupId === undefined) {
          object.position.x = object.position.x + diffX;
          object.position.y = object.position.y + diffY;
        }
      }
      if (object.groupId !== undefined) {
        object.groupId = mapIds[object.groupId];
      }
    });
    this.store.selectedIds.forEach((id) => {
      this.selectObjectOnThreeScene(this.store.objects[id], false);
    });
    const newStore = produce(this.store, (draft) => {
      draft.selectedIds = [];
      objects.forEach((object) => {
        draft.objects[object.id] = object;
        draft.selectedIds.push(object.id);
      });
    });
    setStore(newStore);
    objects.forEach((object) => {
      addObjectByObjectType(object);
      this.selectObjectOnThreeScene(object, true);
    });
    if (this.saveToHistory) {
      const historyEvent: ClipboardPasteHistory = {
        do: this.event,
        redo: this.event,
        undo: {
          name: AppEventType.RemoveObjects,
          payload: {
            ids: objects.map((object) => {
              return object.id;
            }),
          },
        },
      };
      this.history.push(historyEvent);
    }
    const _newStore = this.getNewStoreWithTopBarPosition(newStore);
    setStore(_newStore);
    refreshReactState(_newStore);
  }
}
